const initialState = {
	list: []
}

const tasksReducer = (state = initialState, action) => {
	switch(action.type) {
	case 'ADD_TASK':
		var updatedState = state
		updatedState.list.push(action.taskData)
		Object.assign({}, updatedState)

		break

	default:
		return state
	}
}

export default tasksReducer



